import React, { memo } from 'react';
import CheckBox from '../../../../Views/CheckBox';
import SizesFiltersBox from '../../../../Views/CatalogViews/SizesFilters';
import LayoutCheckBoxItem from '../../../../Views/CatalogViews/LayoutCheckBoxItem';

const SizesFilters = ({
  sizes = [],
  values = { sizes: [] },
  setValues = () => { },
  openBtnSubmit = () => { },
}) => {
  const name = `sizes`;

  const resetFilter = (e) => {
    setValues({ ...values, [name]: [] });
  }

  const handlerChangeFilter = (e, id) => {
    const value = e.checked;
    if (value === null) return;
    if (value) {
      if (!Array.isArray(values.sizes)) {
        values.sizes = [values.sizes];
      }
      const newValue = values.sizes.filter((el) => el !== id);
      setValues({ ...values, [name]: newValue });
    } else {
      setValues({ ...values, [name]: [...values.sizes, id] });
    }
    openBtnSubmit();
  }

  return (
    <SizesFiltersBox
      resetFilter={resetFilter}
    >
      {sizes.map((el, i) => {
        const id = el.id;
        const checked = values.sizes.includes(id);
        return (
          <LayoutCheckBoxItem key={id}>
            <CheckBox
              key={id}
              checked={checked}
              name={name}
              data-cy={`sizesCheckBox-${el.id}`}
              onChange={(e) => handlerChangeFilter(e, id)}
              variant="light"
              label={el.title}
            ></CheckBox>
          </LayoutCheckBoxItem>
        );
      })}
    </SizesFiltersBox>
  );
};

export default (SizesFilters);
